@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Gowun+Dodum&family=Work+Sans:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gowun+Dodum&display=swap);
.App {
  text-align: center;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.fontFix{
    width: 50%;
    text-align: center;
    color: #e54066;
    font-size: 30px;
    font-family: "Work Sans", sans-serif;
    background-color:#ffe592;
    padding: 20px;
    border-radius: 30px;
}

.fontFix span{
    font-weight: normal;
}

.formContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(244, 236, 252);
    padding: 10px;
    border-radius: 10px;
}

label {
    align-self: flex-start;
    margin-bottom: 5px;
}

input {
    border-radius: 8px;
    margin-bottom: 8px;
}

button {
    color: rgb(236, 217, 238);
    text-shadow: 1px 1px 3px black;
    background-color: rgb(216, 101, 101);
    margin: 0 15px;
    padding: 5px;
    box-shadow: 1px 1px 4px black;
}
body {
    /* font-family: 'Gowun Batang', serif; */
    margin: 0;
    padding: 0;
    background-color: #f9efed;
    background-size: cover;  
}

.mainDiv{
    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.backgroundImage{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: left;
 

}

.introBox{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-right: 50px;
}


.banner {
    font-family: 'Amatic SC', cursive;    
    height: 60vw;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: left;

    font-size: 40px;
    text-align: right;
}

 

.banner h1{
    font-family: 'Amatic SC', cursive;
    font-size: 80px;
}

.banner h2{
    font-size: 30px;
    font-family: 'Work Sans', sans-serif;}

.logoAndWord{
    display:flex;

}

.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.authButtons {
    display: flex;

}

button {
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
    font-family: 'Amatic SC', cursive;    
    font-size: 60px;
    text-align: right;
  }
  
  button:hover {
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
  }
  
  button:active {
    outline: none;
    border: none;
  }
  
  button:focus {
    outline: 0;
  }

.hoverfun:hover span{
    display: none;
}

.hoverfun:hover:after{
    content: " ☑️";
}

h4 {
    text-align: center;
    width: 100px;
    font-family: "Amatic SC", cursive;
    font-size: 30px;
    border-radius: 30px;
    color: #e54066;
    padding: 10px;
    margin: 0 15px;
    background-color: #ffe592;
}

h4:hover{
    background-color: #E5b3f2;
}


nav {
    font-family: 'Amatic SC', cursive;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-left: 40px;
    padding-right: 40px;
    background-color: #F9EFED;
    margin-bottom: 0px;
}
.togetherFront{
    font-family: 'Amatic SC', cursive;
    display: flex;
    font-size: 30px;
}



.logoinNav{
    padding-top: 15px;
    padding-left: 15px;
    width: 7%;
    height: 2%;
}




nav ul {
    width: 200px;
    display: flex;
    justify-content: space-between;
}

nav li {
    list-style: none;
}

nav h3 {
    font-size: 60px;
}


.linksJoinOrCreate{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

a{
    text-decoration: none;
    color: palevioletred;
}
